        opacity: .8;
        }

        .mainTitle:hover {
        opacity: 1;
        }

        .Headline {
        background: 
        -webkit-linear-gradient(
        left, 
        var(--main) 0%,
        var(--ternary) 24%, 
        var(--main) 36%,
        var(--ternary) 46%,
        var(--main) 52%,
        var(--main) 58%,
        var(--main) 67%,
        var(--main) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-size: 300%;
        animation: font-shine 10s infinite linear;
        }

        .Subtitle {
        font-family: var(--titleFont);
        background: 
        -webkit-linear-gradient(
        left, 
        var(--main) 0%,
