        -webkit-text-fill-color: transparent;
        background-size: 300%;
        animation: font-shine 10s infinite linear;
        opacity: .6;
        }

        .Subtitle:hover {
        opacity: 1;
        }

        @keyframes font-shine {
        0% {background-position: 0% 0}
        100% {background-position: -100% 0}
