/* leaf animations */
.leaves {position:relative;top:-50px;width:100%;text-align: right;}

.leaves i {
    display: inline-block;
    width: 200px;
    height: 150px;
    transform: skew(20deg);
    border-radius: 5% 40% 70%;
    box-shadow: inset 0px 0px 1px #222;
    border: 1px solid #333;
    background: linear-gradient(to bottom right, #eaaf77, #f9e260);
    z-index: 1;
    -webkit-animation: falling 6s 0s infinite;
}

.leaves {
animation: falling 6s 0s infinite;
}


.leaves i:nth-of-type(2n) { animation: falling2 6s 0s infinite; }
.leaves i:nth-of-type(3n) { animation: falling3 6s 0s infinite; }


.leaves i:before {
  position: absolute;
  content: '';
